.main_banner-header {
  margin-bottom: 60px;
  text-transform: uppercase; }
  .main_banner-header b {
    font-weight: 800; }

.main_banner-nav, .section-aside {
  position: absolute;
  bottom: 40px;
  left: 0; }
  .main_banner-nav ul, .section-aside ul {
    width: 250px; }
    .main_banner-nav ul li, .section-aside ul li {
      text-transform: uppercase;
      margin-bottom: 10px;
      position: relative;
      width: calc(100% - 30px);
      font-weight: 600;
      font-size: 14px; }
      .main_banner-nav ul li:before, .section-aside ul li:before {
        content: '';
        height: 35px;
        width: 35px;
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        position: absolute;
        top: 8px;
        right: -17px;
        transition: all .25s; }
      .main_banner-nav ul li:last-child, .section-aside ul li:last-child {
        margin-bottom: 0; }
      .main_banner-nav ul li a, .section-aside ul li a {
        line-height: 50px;
        display: block;
        position: relative;
        padding-left: 30px;
        transition: all .25s; }

