/* Small Devices, Tablets */
@media only screen and (max-width : 1023px) {
	.col-xs-1, .col-sm-1, .col-md-1, .col-lg-1,
	.col-xs-2, .col-sm-2, .col-md-2, .col-lg-2,
	.col-xs-3, .col-sm-3, .col-md-3, .col-lg-3,
	.col-xs-4, .col-sm-4, .col-md-4, .col-lg-4,
	.col-xs-5, .col-sm-5, .col-md-5, .col-lg-5,
	.col-xs-6, .col-sm-6, .col-md-6, .col-lg-6,
	.col-xs-7, .col-sm-7, .col-md-7, .col-lg-7,
	.col-xs-8, .col-sm-8, .col-md-8, .col-lg-8,
	.col-xs-9, .col-sm-9, .col-md-9, .col-lg-9,
	.col-xs-10, .col-sm-10, .col-md-10, .col-lg-10,
	.col-xs-11, .col-sm-11, .col-md-11, .col-lg-11,
	.col-xs-12, .col-sm-12, .col-md-12, .col-lg-12,
	.container, .container-fluid {
		padding-left: 15px;
		padding-right: 15px; }
	.row {
		margin-left: -15px;
		margin-right: -15px; }

	h1 {
		font-size: 24px; }
	h2 {
		font-size: 24px; }
	h3 {
		font-size: 18px; }

	body {
		font-size: 16px;
		line-height: 1.6; }

	.btn.active:before {
		position: absolute;
		top: 20px;
		left: calc(50% - 6px);
		margin: 0; }

	.main-wrap {
		text-align: center; }

	.main_banner {
		background: #f8f9e7; }
	.main_banner-header {
		margin-bottom: 30px; }
	.main_banner-title {
		text-align: center;
		margin-left: 0 !important;
		width: 100% !important;
		margin-bottom: 30px;
		&:before {
			position: static; } }

	.link-wrap {
		text-align: center; }

	.contact-form {
		padding: 40px 0; }
	.contact-form_form {
		.field-def, .btn {
			width: 100%; }
		.field-def {
			margin-bottom: 20px;
			margin-right: 0; }
		.field-search {
			padding: 0 30px 0 70px; } }
	.contact-form_search .contact-form_form {
		margin-bottom: 15px;
		.fa-search {
			left: 30px; }
		.btn {
			position: static;
			margin-top: 20px; } }

	.section {
		padding: 40px 0; }
	.section-title {
		margin: 25px 0 40px; }
	.section-descr li {
		padding-left: 0;
		&:before {
			position: static;
			margin: 0 auto;
			margin-bottom: 10px; } }
	.section-cart-wrap {
		background: none; }
	.section-cart {
		min-height: auto;
		margin-bottom: 30px; }
	.section-cart_body {
		padding: 30px 25px;
		ul {
			display: none; }
		h3 {
			margin-bottom: 0; }
		br {
			display: none; } }
	.section-nav li {
		margin-bottom: 10px; }
	.section-special {
		left: 15px;
		p {
			font-size: 14px;
			letter-spacing: .6px; } }
	.section-footer {
		margin-top: 0; }
	.section-aside {
		margin: 0 -15px;
		ul {
			margin-bottom: 10px;
			li {
				width: 100%;
				&.active:before, &:hover:before, &:before {
					content: none; }
				a {
					padding: 0;
					span {
						display: none; } } } }
		.link {
			margin: 0 auto; } }
	.section-with_opa {
		width: 100%;
		font-size: 18px;
		margin-bottom: 30px;
		padding: 0;
		&:nth-last-child(-n+3) {
			margin-bottom: 30px; }
		&:last-child {
			margin-bottom: 0; }
		span {
			position: static;
			font-size: 24px; } }
	.section_wrap {
		display: block;
		& > div:last-child .section_block {
			margin-bottom: 0; }
		li {
			width: 100%;
			margin-bottom: 25px;
			& > p {
				display: none; }
			&:nth-last-child(-n+2) {
				margin-bottom: 25px; }
			&:last-child {
				margin-bottom: 0; } } }
	.section_block {
		width: 100%;
		box-shadow: none;
		padding: 0;
		background-color: transparent;
		margin-bottom: 30px;
		&:last-child {
			margin-bottom: 0; }
		&:before, &:after {
			content: none; }
		.link {
			display: none; }
		& > p {
			&:before, &:after {
				left: calc(50% - 5px);
				top: -20px; } } }
	.section-form {
		label {
			width: 100%;
			margin-bottom: 10px; }
		.ui-selectmenu-button, input {
			top: 0;
			width: 100%; }
		.control-label {
			margin-bottom: 30px;
			width: 100%;
			&:nth-last-child(-n+2) {
				margin-bottom: 30px; }
			&:last-child {
				margin-bottom: 0; } } }
	.sectin_map {
		display: none; }
	.section-pagination {
		display: flex;
		justify-content: space-between;
		ul {
			display: inline-flex; }
		li {
			position: absolute; }
		.active {
			font-size: 40px;
			transform: none;
			line-height: 1.6;
			position: static;
			&:after, &:before {
				content: none; } }
		.prev, .next {
			display: inline-block;
			cursor: pointer; } }

	.pagination-text {
		background-color: transparent;
		box-shadow: none;
		.section-cart-wrap {
			padding: 0;
			background: transparent;
			h3 {
				margin-bottom: 10px; } } }

	.main-footer_top {
		.flex-center {
			justify-content: center;
			float: none; }
		.main-footer_nav {
			ul {
				display: block; } } }
	.main-footer_logo {
		margin: 0;
		margin-bottom: 15px; }
	.main-footer_name {
		.name {
			p {
				font-size: 13px; }
			span {
				font-size: 10px;
				vertical-align: baseline; } }
		& > p {
			font-size: 11.4px; } }
	.main-footer_social {
		float: none;
		margin-top: 30px;
		display: flex;
		justify-content: space-around;
		a {
			margin: 0;
			width: 40px; } }
	.main-footer_header {
		text-align: left; }
	.main-footer_nav {
		justify-content: space-between; }
	.main-footer_nav-col {
		text-align: left;
		&:last-child {
			display: none; } } }


