/* Medium Devices, Desktops */
@media only screen and (min-width : 1920px) {

	h1 {
		font-size: 39px; }
	h2 {
		font-size: 29px; }
	h3 {
		font-size: 20px; }

	body {
		font-size: 18px;
		line-height: 1.778; }

	.bg-woman {
		background-image: url(../img/woman-1920.png);
		background-size: 30%;
		background-position-x: calc(100% - 50px); }
	.bg-woman_gad {
		background-image: url(../img/woman-gad-1920.png);
		background-size: 40%;
		background-position-x: calc(100% - 50px); }

	.correct-bg .section-cart-wrap {
		padding-bottom: 300px;
		margin-bottom: -85px; }

	.main_banner {
