	.section-cart {
		min-height: {} }
	.section-opa_wrap {
		display: none; }
	.section_table {
		display: block; }
	.section-nav {
		margin-top: 30px;
		margin-bottom: 50px;
		li {
			font-size: 22px; }
		.active a {
			padding-bottom: 10px; } }
	.section-cart_body {
		padding: 30px 58px;
		h3 {
			font-size: 22px; }
		li {
			margin-bottom: 25px;
			&:after, &:before {
				top: 5px; } }
		br {
			display: none; } }
	.section-footer {
		margin-bottom: -35px; }
	.section-cart-wrap {
		.link {
			float: right; } }
	.section_wrap {
		> div .section_block {
			width: 420px;
			line-height: 1.5;
			min-height: 150px; } }
	.section_block {
		width: 48.5%;
		margin-bottom: 40px;
		& > p {
			&:after, &:before {
				top: 5px; } }
		.link {
			position: absolute;
			bottom: 20px; } }
	.section-aside ul {
		margin-bottom: 10px;
		li {
			font-size: 22px;
			margin-bottom: 3px;
			a span {
				font-size: 20px; } } }
	.section-pagination {
		font-size: 60px;
		font-weight: bold;
		.active {
			font-size: 90px;
			transform: translate(0, 53px);
			&:after, &:before {
				font-size: 60px;
				bottom: 25px; } } }
	.section-form {
		label {
			width: auto;
			min-width: 195px;
			margin-right: 90px; } }

	.contact-form_form {
		.field-search {
			padding-right: 400px; } }
	.contact-form_search {
		.contact-form_form {
			padding-right: 20px;
			.btn {
				display: block; } } }

	.pagination-text {
		.section-cart-wrap {
			padding: 90px 200px; } }

	.link-wrap {
		padding-left: 0; }
	.link {
		display: inline-block; }

	.main-footer_nav {
		font-size: 14px; }
	.main-footer_name {
		.name span {
			font-size: 13px; }
		& > p {
			font-size: 15px; } }
	.main-footer_social a {
		width: 40px; }
	.main-footer_top .main-footer_nav {
		ul {
			justify-content: space-between; }
		li {
			padding: 0; } }
	.main-footer_bottom {
		padding-bottom: 20px; }
	.main-footer_header {
		margin-top: 25px; }

	.cor-p-r {
		padding-right: 20px; }
	.cor-p-l {
		padding-left: 20px; }
	.m-t75 {
		margin-top: 0; }

	.mark-60 {
		.mark:before {
			width: 70px;
			height: 70px; } }
	.mark-check {
		&:before, &:after {
			content: '';
			left: -25px;
			top: 5px; } } }

/* Large Devices, Wide Screens */
@media only screen and (min-width : 2543px) {

	h1 {
		font-size: 44px; }
	h2 {
		font-size: 34px; }
	h3 {
		font-size: 24px; }

	body {
		font-size: 20px;
		line-height: 1.9; }

	.main_banner {
		padding: 80px; }
	.main_banner-title {
		&:nth-child(odd) {
			width: 450px; }
		&:nth-child(even) {
			width: 700px; } }
	.main_banner-nav, .section-aside {
		bottom: 100px;
		padding-right: 100px; }

	.section-footer {
		p, .link {
			font-size: 24px; } }
	.section_table td {
		width: 201px; }
	.section_wrap > div .section_block {
		width: 535px; }
	.section-form label {
		min-width: 250px; }

	.contact-form_form {
		text-align: right; } }
