.mark-orange {
	.mark:before {
		background-color: #ff8400; } }
.mark-check {
	&:after, &:before {
		position: absolute;
		width: 3px;
		left: 10px;
		top: 0;
		-webkit-border-radius: 5px;
		border-radius: 5px; }
	&:before {
		height: 15px;
		transform: rotate(40deg) translate(3px, 0px); }
	&:after {
		height: 10px;
		transform: rotate(-50deg) translate(-10px, 1px); } }

.form-orange {
	.ui-selectmenu-button, input, .ui-menu {
		border-color: #ff8400; } }
.ui-menu {
	background-color: #fff;
	border-width: 3px;
	border-style: solid;
	border-color: #ff8400;
	border-top: none;
	&:after, &:before {
		position: absolute;
		content: '';
		width: 3px;
		height: 40px;
		display: block;
		background-color: #ff8400;
		top: -30px; }
	&:after {
		right: 0; }
	&:before {
		left: 0; } }
.ui-menu-item {
	border-bottom: 1px solid  #ff8400;
	&:last-child {
		border: none; } }
.ui-menu-item .ui-state-active {
	font-weight: 600; }


.main-footer_top, .main-footer_bottom {
	padding: 40px 0; }
.main-footer_top {
	background: #f6f6f6;
	.flex-center {
		float: left; }
	.main-footer_nav {
		display: block;
		ul {
			display: flex;
			justify-content: center;
			flex-flow: wrap row; }
		li {
			padding: 0 17px;
			a {
				border-bottom-width: 2px;
				border-bottom-style: solid;
				padding-bottom: 5px; }
			&:nth-child(9n+1) a {
				border-bottom-color: #fb4633; }
			&:nth-child(9n+2) a {
				border-bottom-color: #f1d697; }
			&:nth-child(9n+3) a {
				border-bottom-color: #9be5ff; }
			&:nth-child(9n+4) a {
				border-bottom-color: #0abbb9; }
			&:nth-child(9n+5) a {
				border-bottom-color: #ffb400; }
			&:nth-child(9n+6) a {
				border-bottom-color: #a1c918; }
			&:nth-child(9n+7) a {
				border-bottom-color: #8552ba; }
			&:nth-child(9n+8) a {
				border-bottom-color: #2112e5; }
			&:nth-child(9n+9) a {
				border-bottom-color: #01b209; } } } }
.main-footer_bottom {
	background: #e8e8e8; }
.main-footer_header {
	text-transform: uppercase; }
.main-footer_nav {
	display: flex;
	justify-content: space-around;
	flex-flow: wrap row;
	font-size: 12px;
	text-transform: uppercase;
	font-weight: 600;
	line-height: 50px; }
.main-footer_nav-col {
	display: inline-block;
	li a {
		border-bottom: 1px solid  #c1c1c1;
		&:hover {
			color: rgba(0, 0, 0, .6);
			border-bottom: none; } } }
.main-footer_copyright {
	font-size: 14px;
	color: #969696;
	margin-top: 30px; }
.main-footer_logo {
	margin-right: 50px; }
.main-footer_name {
	& > p {
		font-weight: 600;
		font-style: italic;
		font-size: 13px;
		color: #0d47a1; }
	.name {
		font-family: 'PT Serif', serif;
		font-weight: bold;
		line-height: 1;
		p {
			display: inline-block;
			text-transform: uppercase;
			padding-right: 10px;
			margin-right: 10px;
			border-right: 1px solid  #000; }
		span {
			font-size: 11px;
			vertical-align: super; } } }
.main-footer_social {
	float: right;
	a {
		width: 30px;
		display: inline-block;
		margin-right: 30px; } }

